.media-ov2:after{
    content: "";
    background: #000;
    position: absolute;
    left: 0;
    top: 0;
    width: 0;
    height: 0;
    opacity: 0.4;
    z-index: 1;
    transition: all 0.3s linear;
}
/*effect 1*/
.media-ov1:before {
    width: 100%;
    height: 100%;
    opacity: 0;
}
.media-ov1:hover:before,
.ttr-box:hover .media-ov1:before {
    opacity: 0.5;
}
/*effect 7*/
.media-ov2:before,
.media-ov2:after {
    height: 100%;
}
.media-ov2:after {
    left: auto;
    right: 0;
}
.media-ov2:hover:before,
.media-ov2:hover:after,
.ttr-box:hover .media-ov2:before,
.ttr-box:hover .media-ov2:after {
    width: 50%;
}

.media-effect {
    position: relative;
    overflow: hidden;
    display: block;
	vertical-align: middle;
}
.media-effect img {
    display: block;
    margin: 0;
    width: 100%;
    height: auto;
    box-shadow: 0 0 0 rgba(0, 0, 0, 0);
    -webkit-box-shadow: 0 0 0 rgba(0, 0, 0, 0);
    -moz-box-shadow: 0 0 0 rgba(0, 0, 0, 0);
    transition: all 0.25s;
    -moz-transition: all 0.25s;
    -webkit-transition: all 0.25s;
    -o-transition: all 0.25s;
}
/* >image-opacity */
.media-effect.opacity img:hover {
    opacity: 0.8;
    -moz-opacity: 0.8;
    -webkit-opacity: 0.8;
}
/* >image-zoom */
.media-effect.zoom-slow img {
    transition: all 10s;
    -moz-transition: all 10s;
    -webkit-transition: all 10s;
    -o-transition: all 10s;
}
.media-effect.zoom-slow:hover img {
    -webkit-transform: scale(1.2);
    transform: scale(1.2);
}
