.ttr-separator-outer {
    overflow: hidden;
}
.ttr-separator {
    display: inline-block;
    height: 2px;
    width: 80px;
    margin-bottom: 10px;
    position: relative;
}
.ttr-separator.style-liner {
    width: 20px;
}
.ttr-separator.style-icon {
    width: 30px;
    height: auto;
    text-align: center;
    font-size: 20px;
}
.ttr-separator[class*="style-"]:after,
.ttr-separator[class*="style-"]:before {
    content: "";
    position: absolute;
    top: 50%;
    left: 40px;
    width: 70px;
    height: 2px;
    background: #eee;
    -webkit-transform: translateY(-50%);
    transform: translateY(-50%);
}
.ttr-separator[class*="style-"]:before {
    left: auto;
    right: 40px;
}
.ttr-separator.style-skew {
    width: 15px;
    height: 10px;
    margin-left: 1px;
    margin-right: 1px;
    -webkit-transform: skewX(-10deg);
    transform: skewX(-10deg);
}
.ttr-separator.style-skew[class*="style-"]:after,
.ttr-separator.style-skew[class*="style-"]:before {
    width: 80px;
    height: 4px;
    left: 20px;
    -webkit-transform: translateY(-50%) skewX(-10deg);
    transform: translateY(-50%) skewX(-10deg);
}
.ttr-separator.style-skew[class*="style-"]:before {
    right: 20px;
    left: auto;
}
