	padding:30px;
	position: relative;
    z-index: 1;
	overflow: hidden;
}
.widget-newslatter p{
	font-style: italic;
	font-size:15px;
	margin-bottom: 15px;
}
.widget-newslatter .form-control {
    background: #ffffff none repeat scroll 0 0;
    border: 1px solid #d0d0d0;
    height: 45px;
    margin-bottom: 0;
    padding: 5px 15px;
}
.widget-newslatter .btn{
	padding:10px 15px;
	font-weight: 700;
	width:50px;
}
.news-box:before {
	font-family: themify;
	content: "\e75a";
	font-size: 80px;
	font-weight: 700;
	opacity: 0.05;
	position: absolute;
	right: 20px;
	top: -10px;
	z-index: -1;
	opacity: 0.05;
}
.news-box form{
	position:relative;
}

